<template>
    <div class="px-1">
        <p class="font-weight-bold mt-3 pt-5">SALDO <span style="float: right;">Rp 50.000.000,-</span></p>
        <div class="accordion" id="accordionExample">
            <div class="card" v-for="data in transactions" :key="data.transaction_id">
                <div class="card-header">
                    <div class="d-flex align-items-center justify-content-between">
                        <div class="text-info font-weight-bold" data-toggle="collapse" :data-target="'#'+data.transaction_id" aria-expanded="true" aria-controls="collapseOne">
                            {{data.transaction_id}}
                        </div>
                        <div class="btn-group float-right" role="group" aria-label="Basic example">
                            <router-link v-for="(button, key) in data.metadata.buttons" :key="'button-'+key" :to="{name:button.link.routeName, params:button.link.params}" :class="'btn btn-'+button.size+' btn-'+button.variantColor">{{button.label}}</router-link>
                            <!-- <button type="button" class="btn btn-sm btn-info" @click="buka_struk_transaksi(data)">Details</button>
                            <button type="button" class="btn btn-sm btn-success" @click="buka_map(data.id)">Map</button> -->
                        </div>
                    </div>
                </div>
                <div :id="data.transaction_id" class="collapse" data-parent="#accordionExample">
                    <div v-for="product in data.products" :key="'product-'+product.id_barang" class="col shadow-sm py-2 nopaddingleft">
                        <div class="col">
                            {{product.nama_barang}}
                        </div>
                        <div class="col mt-2">
                            <span>{{product.jumlah}} x {{toCurrency(product.harga_jual)}}</span>
                            <span style="float:right">{{toCurrency(product.jumlah*product.harga_jual)}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:"History",
    data(){
        return {
            transactions:null
        }
    },
    created(){
        this.axios.get(this.$store.state.url_api + "users/transactions", {
            headers : {
                "Accept" : "Application/json",
                "Authorization" : "Bearer " + localStorage.token
            }
        }).then(response => {
            this.transactions = response.data.data.transactions;
        })
    },
    methods:{
        toCurrency(angka) {
            angka += '';
			// angka = angka.replace(".",",");
			var x = angka.split('.');
			var x1 = x[0];
			var x2 = x.length > 1 ? '.' + x[1] : '';
			var rgx = /(\d+)(\d{3})/;
			while (rgx.test(x1)) {
				x1 = x1.replace(rgx, '$1' + '.' + '$2');
			}
			return 'Rp'+x1 + x2;
        },
    }
}
</script>